<style lang="stylus">
@import '../assets/style/_base.styl'

.modal-wrapper
  width 100%
  padding-top 10%

@media only screen and (max-width: 600px)
  .modal-wrapper
    padding-top calc(500px + 20%) !important
  .modal-container
    height 100%

.modal-container
  width 500px
  height auto
  display flex
  align-items center
  flex-direction column
  margin 0px auto
  padding 20px 30px
  background-color #fff
  border-radius 2px
  box-shadow 0 2px 8px rgba(0, 0, 0, 0.33)
  transition all 0.3s ease
  font-family Helvetica, Arial, sans-serif

.modal
  width 100%
</style>
<template>
  <transition name="modal">
    <BackgroundMask>
      <div class="modal-wrapper modalHeight">
        <div class="modal-container">
          <component class="modal" :is="modal"></component>
        </div>
      </div>
    </BackgroundMask>
  </transition>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
import BackgroundMask from "@/components/reuse/BackgroundMask";
import CardModal from "@/components/form/CardModal";
import CartModal from "@/components/form/CartModal";
import PaymentModal from "@/components/form/PaymentModal";
export default {
  components: { BackgroundMask, CardModal, CartModal, PaymentModal },
  props: {},
  data() {
    return {
      innerHeight: ""
    };
  },
  computed: {
    ...mapGetters(["modal"])
  },
  watch: {},
  created() {},
  mounted() {
    this.getFullHeight();
    this.onResize();
  },
  destroyed() {
    this.emptyModalForm();
    this.getFullHeight();
    this.onResize();
  },
  methods: {
    ...mapActions(["setModalData"]),
    emptyModalForm() {
      this.setModalData("");
    },
    getFullHeight() {
      this.innerHeight = window.innerHeight;
      const header = document.querySelector("header");
      if (header) {
        let bannerSection = document.querySelector(".modalHeight");
        let paddingTop = this.innerHeight / 1.75 + "px";
        bannerSection.style.paddingTop = paddingTop;
      }else return 
    },
    onResize() {
      this.$nextTick(() => {
        window.addEventListener("resize", () => {
          this.getFullHeight();
        });
      });
    }
  }
};
</script>